@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.calculator {
  font-family: 'Roboto', sans-serif;
  margin: 30px auto;
  width: 320px;
  background: silver;
  overflow: hidden;
  padding: 5px 0 15px 0;
}

.numBoard {
  width: 180px;
  display: inline-block;
  float: left;
  border: 1px solid black;
  margin-left: 8px;
}

.opBoard {
  width: 120px;
  display: inline-block;
  float: right;
  border: 1px solid black;
  margin-right: 8px;
}

.displayContainer {
  margin: 5px auto;
  width: 296px;
  height: 100px;
  background: #f59218;
  line-height: 50px;
  border: 4px solid black;
  text-align: right;
}

.prevDisplay {
  height: 50px;
  font-size: 1.2em;
  font-style: italic;
  white-space: nowrap;
  padding-right: 10px;
  overflow: hidden;
}

.prevDisplay .inPrevDisplay {
  float: right;
}

.currDisplay {
  height: 50px;
  font-size: 2em;
  padding-right: 10px;
  overflow: hidden;
}

.button {
  height: 58px;
  width: 58px;
  background: gray;
  display: inline-block;
  border: 1px solid black;
  line-height: 58px;
}

.button:hover {
  cursor: pointer;
  color: white;
}

.button__zero {
  width: 118px;
  border: 1px solid black;
}

.button__AC {
  width: 118px;
  background: #21d1a2;
}

.button__equals {
  height: 118px;
  float: right;
  line-height: 118px;
}

.button__numBoard {
  background: rgba(120, 114, 108, 0.9);
}

.button__opBoard {
  background: rgba(120, 114, 108, 0.6);
}
